* {
    color: #ffffff;
    margin: 0;
    padding: 0;
}

.header {
    text-align: center;
    color: skyblue;
    font-size: 50px;
    padding-top: 100px;
}

body {
    background-image: url(https://cdn.wallpapersafari.com/93/32/d5Tgo9.jpg);
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    min-height: 100%;
}

.app {
    background-image: url(https://www.pixelstalk.net/wp-content/uploads/2016/07/1080p-Wallpapers-Full-HD-Download.jpg);
    width: 95%;
    min-width: 200px;
    height: min-content;
    border-radius: 15px;
    backdrop-filter: 20px;
    padding: 20px;
    box-shadow: 10px 1-px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.score-section {
    display: flex;
    font-size: 24px;
    align-items: center;
}

.question-text {
    margin-bottom: 12px;
}

.question-count {
    margin-bottom: 20px;
}

.question-count span {
    font-size: 60px;
}

.anwer-section {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
}

.anwer-section button {
    width: 100%;
    font-size: 16px;
    color: #ffffff;
    background: linear-gradient(83deg, #0c5a79 0%, #910a42 80%);
    border-radius: 15px;
    display: flex;
    padding: 10px;
    justify-content: flex-start;
    align-items: center;
    border: 3px solid teal;
    cursor: pointer;
}